import React from "react";
import Header from "./component/header";
import About from "./pages/about";
import Contact from "./pages/contact";
import Home from "./pages/Home";
import NewsDetail from "./pages/newsDetail";
import react from "react";
import { BrowserRouter, Switch, Route, Redirect} from "react-router-dom";
import Post from "./pages/post";
import Update from "./pages/update";
import Expost from "./pages/latihan_post"

function App() {
  const [tagline, setTagline] = react.useState("infoSmk");
  return (
    <BrowserRouter>
      <div className="antialiased h-screen w-full bg-gray-50 px-16">
        <div className="h-20 w-full ">
          <Header tagline={tagline}></Header>
        </div>

        {/* <nav>
          <ul className="flex w-full items-center justify-end text-lg">
            <li className="mr-5 hover:text-red-500">
              <Link to="/">Home</Link>
            </li>
            <li className="mr-5 hover:text-red-500">
              <Link to="/about">About</Link>
            </li>
            <li className="mr-5 hover:text-red-500">
              <Link to="/contact">Contact</Link>
            </li>
          </ul>
        </nav>       */}

        <Switch>
          <Route path="/berita" exact>
            <Home tagline={tagline} setTagline={setTagline}></Home>
          </Route>
          <Route exact path="/berita/:slug">
           <NewsDetail></NewsDetail>
          </Route>
          <Route path="/berita/update/:slug">
           <Update></Update>
          </Route>
          <Route path="/about">
            <About></About>
          </Route>
          <Route path="/contact">
            <Contact></Contact>
          </Route>
          <Route path="/post-artikel">
            <Post></Post>
          </Route>
          <Route path="/latihan-post">
           <Expost></Expost>
          </Route>
          
        </Switch>
        {/* <Redirect from="/contact" to="/about"/> */}
        <Redirect from="*" to="/berita"/>
      </div>
    </BrowserRouter>
  );
}

export default App;
