import axios from "axios";
import React from "react";
import swal from "sweetalert";
import {useHistory} from "react-router-dom"

export default function Post() {
  const [imagePreview, setImagePreview] = React.useState("");
  const [disableSubmit, setDisableSubmit] = React.useState(true);
  const [isLoading, setIsLoading] = React.useState(false);
  const [values, setValues] = React.useState({
    judul: "",
    Artikel: "",
    thumbnail: "",
    user_id: 2,
    password: "",
    password_confirmation: "",
  });
  const [error, setError] = React.useState({
    judul: "",
    Artikel: "",
    thumbnail: "",
    user_id: 2,
    password: "",
    password_confirmation: "",
  });
  let history = useHistory()
  const handleChange = (e) => {
    const key = e.target.id;
    const value = e.target.value;
    setValues((values) => ({
      ...values,
      [key]: value,
    }));

    if (key === "judul") {
      if (value === "") {
        return (error.judul = "Judul tidak boleh kosong");
      } else {
        return (error.judul = "");
      }
    }

    if (key === "artikel") {
      if (value === "") {
        error.artikel = "Konten Artikel tidak boleh kosong";
      } else if (
        value.replace(/\s/g, "").length < 10 ||
        value.replace(/\s/g, "").length > 10
      ) {
        error.artikel = "konten artikel minimal 10 huruf";
      } else {
        error.artikel = "";
      }
    }

    if (key === "password") {
      if (value === "") {
        error.password = "Password tidak boleh kosong";
      } else {
        error.password = "";
      }
    }

    if (key === "password_confirmation") {
      if (value === "") {
        error.password_confirmation =
          " konfirmasi password  tidak boleh kosong";
      } else {
        error.password_confirmation = "";
      }
    }
    disableSubmitHandle();
  };
  const disableSubmitHandle = () => {
    if (
      error.judul === "" &&
      error.artikel === "" &&
      error.password === "" &&
      error.password_confirmation === "" &&
      error.thumbnail === ""
    ) {
      return setDisableSubmit(false);
    } else {
      return setDisableSubmit(true);
    }
  };
  const handleUpload = (e) => {
    e.preventDefault();
    let reader = new FileReader();
    setValues((values) => ({
      ...values,
      thumbnail: e.target.files[0],
    }));
    reader.onloadend = () => {
      if (reader.readyState === 2) {
        setImagePreview(reader.result);
      }
    };
    reader.readAsDataURL(e.target.files[0]);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const url = `https://api-react-2.herokuapp.com/api/artikel`;

    const formData = new FormData();
    formData.append("judul", values.judul);
    formData.append("artikel", values.artikel);
    formData.append("thumbnail", values.thumbnail);
    formData.append("user_id", values.user_id);
    setIsLoading(true);
    // formData.append("password", values.password )
    // formData.append("password_confirmation", values.password_confirmation)
    try {
      let result = await axios.post(url, formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      });
      console.log(result);
      swal("Good job!", "Your Post has been uploaded", "success");
      console.log("berhasil");
      setIsLoading(false);
      history.push('/home')
    } catch (err) {
      swal("Sorry", "Failed to post", "error");
      console.log(err);
      setIsLoading(false);
    }
  };
  console.log(values);
  return (
    <React.Fragment>
      <h1>Post Artikel Baru</h1>
      <form onSubmit={handleSubmit} className="mt-5">
        <div className="grid gap-1 gap-x">
          <label className="uppercase" htmlFor="judul">
            Judul Artikel
          </label>
          <input
            className="border px-5 py-2"
            name="judul"
            value={values.judul}
            onChange={handleChange}
            onBlur={handleChange}
            id="judul"
            type="text"
          />
        </div>

        <p className="text-red-500 italic font-bold">{error.judul}</p>

        <div className="grid gap-1 gap-x">
          <label className="uppercase" htmlFor="artikel">
            Konten Artikel
          </label>
          <textarea
            className="border px-5 py-2"
            name="artikel"
            id="artikel"
            cols="30"
            rows="10"
            onChange={handleChange}
            onBlur={handleChange}
            value={values.artikel}
          ></textarea>
          <p className="text-red-500 italic font-bold">{error.artikel}</p>
          <div className="grid gap-1 gap-x">
            <label className="uppercase" htmlFor="judul">
              Password
            </label>
            <input
              className="border px-5 py-2"
              name="password"
              value={values.password}
              onChange={handleChange}
              onBlur={handleChange}
              id="password"
              type="password"
            />
          </div>
          <p className="text-red-500 italic font-bold">{error.password}</p>
          <div className="grid gap-1 gap-x">
            <label className="uppercase" htmlFor="judul">
              Password korfimasi
            </label>
            <input
              className="border px-5 py-2"
              name="password_confirmation"
              value={values.password_confirmation}
              onChange={handleChange}
              onBlur={handleChange}
              id="password_confirmation"
              type="password"
            />
          </div>
          <p className="text-red-500 italic font-bold">
            {error.password_confirmation}
          </p>
        </div>
        <div>
          <img className="h-40" src={imagePreview} alt="" />
        </div>
        <div className="grid gap-1 gap-x">
          {""}
          <label className="uppercase" htmlFor="thumbnail">
            thumbnail Artikel
          </label>
          <input
            name="thumbnail"
            className="border px-5 py-2"
            onChange={handleUpload}
            id="thumbnail"
            type="file"
          />
        </div>
        <div>
          <button
            disabled={disableSubmit}
            type="submit"
            className={`inline-flex p-3 w-full ${
              disableSubmit ? "bg-red-500" : "bg-green-400"
            } flex items-center justify-center uppercase font-bold text-white`}
          >
            {isLoading ? "Menyimpan ..." : "Simpan"}
          </button>
        </div>
      </form>
    </React.Fragment>
  );
}
