import React from 'react'
import {useHistory} from "react-router-dom"

export default function About(){
    const history = useHistory()
    console.log('history' , history)
    return (
        <React.Fragment>
            <p>ini adalah about page</p>

            <button onClick={()=> {
                history.push("/home")
            }} className='inline-block px-2 py-3 bg-red-500 hover:bg-yellow-300 text-white'> Back Home</button>
            <br />
            <button onClick={()=> {
                history.goBack("/")
                }}className='inline-block px-2 py-3 bg-blue-500 hover:bg-green-300 text-white'> Go Back</button>
        </React.Fragment>
    )
}