import React from 'react'

export default function Aside(props){
    const HandleInputHeader =(e) =>{
        e.preventDefault()

        return props.setHeader(e.target.value)
    }
    const HandleChangeHeader = (e)=> {
        e.preventDefault()
        if(props.header === ""){
           return props.setHeader('MQNEWS')
        }
    }
    return(
        <asside2 ClassName="w-full">
            <input
                placeholder="Seacrh"
                value={props.header}
                className="border mt-5 w-full px-2 py-3"
            type="text" 
            onInput={HandleInputHeader}
            onBlur={HandleChangeHeader}
            />
        </asside2>
    )
}