import React from "react";
import {
  BrowserRouter,
  Switch,
  Route,
  NavLink
} from "react-router-dom";
function Header(props) {
  const [count, setCount] = React.useState(0);
  const [errorMessage, setErrorMessage] = React.useState("");

  return (
    <header className="h-full w-full flex items-center justify-between">
      <div>
        <h1 className="text-3xl font-semibold">MQNEWS

        </h1>
        <p className="text-md font-semibold">{props.tagline}</p>
      </div>
      <nav>
          <ul className="flex w-full items-center justify-end text-lg">
            <li className="mr-5 hover:text-red-500">
              <NavLink exact activeClassName="text-xl font-bold "  to="/berita">Home</NavLink>
            </li>
            <li className="mr-5 hover:text-red-500">
              <NavLink activeClassName="text-xl font-bold" to="/about">About</NavLink>
            </li>
            <li className="mr-5 hover:text-red-500">
              <NavLink activeClassName="text-xl font-bold" to="/contact">Contact</NavLink>
            </li>
            <li className="mr-5 hover:text-red-500">
              <NavLink activeClassName="text-xl font-bold" to="/post-artikel">Post Artikel</NavLink>
            </li>
            <li className="mr-5 hover:text-red-500">
              <NavLink activeClassName="text-xl font-bold" to="/latihan-post">Expost</NavLink>
            </li>
          </ul>
        </nav>      

      {/* <div>
        <p className="text-xl font-bold text-center">{count}</p>
        <p className="text-red-500 font-bold italic">{errorMessage}</p>
        <div>
          <button
            onClick={() => {
              setErrorMessage("");
              return setCount((count) => count + 1);
            }}
            className="inline-flex bg-blue-500 px-3 py-3 text-white rounded-lg"
          >
            Tambah
          </button>
          <button
            onClick={() => {
              if (count === 0) {
                return setErrorMessage(
                  "Nilai sudah 0 , anda tidak bisa mengurangi lagi"
                );
              }
              return setCount((count) => count - 1);
            }}
            className="inline-flex bg-red-500 px-3 py-3 text-white rounded-lg"
          >
            Kurang
          </button>
        </div>
      </div> */}
    </header>
  );
}

export default Header;