import axios from "axios";
import React from "react";
import { useParams } from "react-router-dom";
import { useHistory } from "react-router";
export default function NewsDetail() {
  const [artikel, setArtikel] = React.useState();
  let { slug } = useParams();
  let history = useHistory();
  const getArtikelDetail = async () => {
    const url = `https://api-react-2.herokuapp.com/api/artikel/${slug}`;
    try {
      let response = await axios.get(url);
      console.log(response.data.data);
      let data = response.data.data;
      setArtikel(response.data.data);
      if (data === undefined) {
        return history.push("/berita");
      }
    } catch {
      history.push("/berita");
    }
  };

  React.useEffect(() => {
    getArtikelDetail();
  }, []);

  return (
    <div className="mt-10">
      <h1 className="text-xl font-bold uppercase mb-10">{artikel?.judul}</h1>
      <img src={artikel?.thumbnail} alt="" />
      <p className="text-justify leading-relaxed">{artikel?.artikel} </p>
    </div>
  );
}
