import React, { Component } from "react";
import axios from "axios";
import swal from "sweetalert";

export default function Login() {
  const [disableSubmit, setDisableSubmit] = React.useState(true);
  const [isLoading, setIsLoading] = React.useState(false);

  const [values, setValues] = React.useState({
    username: "",
    name: "",
    jenis_kelamin: "",
    email: "",
    password: "",
    password_confirmation: "",
  });
  const [error, setError] = React.useState({
    username: "",
    name: "",
    jenis_kelamin: "",
    email: "",
    password: "",
    password_confirmation: "",
  });

  const handleChange = (e) => {
    const key = e.target.name;
    const value = e.target.value;
    setValues((values) => ({
      ...values,
      [key]: value,
    }));
    if (key === "username") {
      if (value === "") {
        return (error.username = "username Tidak Boleh Kosong");
      } else {
        error.username = "";
      }
    }
    if (key === "name") {
      if (value === "") {
        return (error.name = "name Tidak Boleh Kosong");
      } else {
        error.name = "";
      }
    }
    // if (key === "jenis_kelamin") {
    //   if (value === "") {
    //     return (error.jenis_kelamin =
    //       "Pilih Jenis Kelamin Tnameak Boleh Kosong");
    //   } else {
    //     error.jenis_kelamin = "";
    //   }
    // }
    if (key === "email") {
      if (value === "") {
        return (error.email = "email Tidak Boleh Kosong");
      } else {
        error.email = "";
      }
    }
    if (key === "password") {
      if (value === "") {
        return (error.password = "password Tidak Boleh Kosong");
      } else if (value.replace(/\s/g, "").length < 8) {
        error.password = "Password Minimal 8 Karakter"; 
      }else {
        error.password = "";
      }
    }
    if (key === "password_confirmation") {
      if (value === "") {
        return (error.password_confirmation =
          "Password Konfigurasi Tidak Boleh Kosong");
      } else if (values.password !== values.password_confirmation) {
        error.password_confirmation = "Password harus sama";
      } else {
        error.password_confirmation = "";
      }
    }
    disableSubmitHandle();
  };

  const disableSubmitHandle = () => {
    console.log(values);
    if (
      values.username === "" &&
      values.name === "" &&
      values.jenis_kelamin === "" &&
      values.email === "" &&
      values.password === "" &&
      values.password_confirmation === ""
    ) {
      return setDisableSubmit(false);
    } else {
      return setDisableSubmit(true);
    }
  };

  console.log(values);
    const handleSubmit = async (e) => {
    e.preventDefault();
   
    const url = `http://belajar-react.smkmadinatulquran.sch.id/api/register`;
    const formData = new FormData();
    formData.append("username", values.username);
    formData.append("name", values.name);
    formData.append("jenis_kelamin", values.jenis_kelamin);
    formData.append("email", values.email);
    formData.append("password", values.password);
    formData.append("password_confirmation", values.password_confirmation);
    setIsLoading(true);
    try {
      let result = await axios.post(url, formData, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      });
      swal("Good job!", "You clicked the button!", "success");
      setIsLoading(false);
      console.log(result);
    } catch (err) {
      console.log(err);
      setIsLoading(false);
      swal("Oh No!", "Something Went Wrong", "error");
        
    }
  };

  return (
    <div className="flex justify-center">
      <form
        onSubmit={handleSubmit}
        className="m-3 mt-4 relative bg-white shadow-sm p-8 rounded-lg text-gray-700 w-1/4"
      >
        <h1 className="flex-1 mb-6 font-bold capitalize">
          Please fill the forum below
        </h1>
        <div className="flex flex-row items-center flex-wrap">
          <label htmlFor="username">Username</label>
          <input
            className="flex-1 pt-1 pb-2 rounded-3xl mt-1 mr-2 ml-2"
            type="text"
            name="username"
            name="username"
            value={values.username}
            onChange={handleChange}
            onBlur={handleChange}
          />
        </div>
        <p className="text-red-500 italic font-bold">{error?.username}</p>
        <div className="flex flex-row items-center flex-wrap">
          <label htmlFor="name">name</label>
          <input
            className="flex-1 pt-1 pb-2 rounded-3xl mt-1 mr-2 ml-2"
            type="text"
            name="name"
            name="name"
            value={values.name}
            onChange={handleChange}
            onBlur={handleChange}
          />
        </div>
        <p className="text-red-500 italic font-bold">{error?.name}</p>
        <div>
          <div className="form-check">
            <label>
              <input
                type="radio"
                value="Laki-laki"
                name="jenis_kelamin"
                id="laki_laki"
                checked={values.jenis_kelamin === "Laki-laki"}
                onChange={handleChange}
              />
              Male
            </label>
          </div>

          <div className="form-check">
            <label>
              <input
                type="radio"
                value="perempuan"
                name="jenis_kelamin"
                id="perempuan"
                checked={values.jenis_kelamin === "perempuan"}
                onChange={handleChange}
              />
              Female
            </label>
          </div>
        </div>
        <div className="flex flex-row items-center flex-wrap mt-3">
          <label htmlFor="email">Email</label>
          <input
            className="flex-1 pt-1 pb-2 rounded-3xl mt-1 mr-2 ml-2"
            type="email"
            value={values.email}
            onChange={handleChange}
            onBlur={handleChange}
            name="email"
            name="email"
          />
        </div>
        <p className="text-red-500 italic font-bold">{error?.email}</p>
        <div className="flex flex-row items-center flex-wrap mt-3">
          <label htmlFor="password">Password</label>
          <input
            className="flex-1 pt-1 pb-2 rounded-3xl mt-1 mr-2 ml-2"
            type="password"
            value={values.password}
            onChange={handleChange}
            onBlur={handleChange}
            name="password"
            name="password"
          />
        </div>
        <p className="text-red-500 italic font-bold">{error?.password}</p>
        <div className="flex flex-row items-center flex-wrap mt-3">
          <label htmlFor="password_confirmation">Password Configuration</label>
          <input
            className="flex-1 pt-1 pb-2 rounded-3xl mt-1 mr-2 ml-2"
            type="password"
            value={values.password_confirmation}
            onChange={handleChange}
            onBlur={handleChange}
            name="password_confirmation"
            name="password_confirmation"
          />
        </div>
        <p className="text-red-500 italic font-bold">
          {error?.password_confirmation}
        </p>
        <div>
          <button
            onClick={() => {
              setIsLoading(true);
            }}
            disabled={disableSubmit}
            type="submit"
            className={`inline-flex p-3 mt-10 w-full ${
              disableSubmit
                ?"bg-red-600"
                :"bg-blue-500 hover:bg-blue-600 focus:bg-blue-700"
            } items-center justify-center uppercase font-bold text-white`}
          >
            {isLoading ? "Login In....." : "Login"}
          </button>
        </div>
      </form>
    </div>
  );
}