import React from "react";
import Artikel from "../../component/artikel";
import Asside from "../../component/asside";
import Loading from "../../component/loading";
import axios from "axios";
export default function Home({ tagline, setTagline }) {
  const [artikels, setArtikels] = React.useState([]);
  const [isLoading, setLoading] = React.useState(false);
  const getArtikles = async () => {
    setLoading(true);
    const url = `https://api-react-2.herokuapp.com/api/artikel`;
    let response = await axios.get(url);
    try {
      let response = await axios.get(url);
      console.log("Artikels", response.data);
      setArtikels(response.data.data.data);
      setLoading(false);
    } catch (err) {
      console.log("err", err);
      setLoading(false);
    }
  };

  React.useEffect(() => {
    getArtikles();
  }, []);

  // const [SiswaRpl] = react.useState(["rifqi,ghozi,muhyi,asep,udin"])

  return (
    <React.Fragment>
      <div className="grid grid-cols-6 ">
        <div className="col-span-5 grid grid-cols-5 gap-5 overflow-auto px-1 py-5">
          {isLoading ? (
            <React.Fragment>
              <Loading></Loading>
              <Loading></Loading>
              <Loading></Loading>
            </React.Fragment>
          ) : (
            artikels.map((ar, index) => (
              <Artikel
                key={index}
                judul={ar.judul}
                link={ar.thumbnail}
                alt={ar.alt}
                konten={ar.artikel}
                slug={ar.slug}
                id={ar.id}
                getArtikles={getArtikles}
              ></Artikel>
            ))
          )}
        </div>
        
        {/* {isLoading ? (true) : (false)} */}

        <div className="col-span-1">
          <Asside setTagline={setTagline} tagline={tagline}></Asside>
        </div>
      </div>
    </React.Fragment>
  );
}
