import React from "react";
import { Link } from "react-router-dom";
import axios from "axios";
import swal from "sweetalert";
function Artikel(props) {
  const deleteHandle = async (id) => {
    const url = `https://api-react-2.herokuapp.com/api/artikel/${id}`;

    try {
      await axios.delete(url);
      swal("Poof! Your imaginary file has been deleted!", {
        icon: "success",
      });
      props.getArtikles();
    } catch (err) {}
  };
  return (
    <article className="border p-5">
      <h1 className="font-semibold">{props.judul}</h1>
      <img className="mt-5" src={props.link} alt={props.alt} />
      <p className="mt-5 truncate">{props.konten}</p>
      <div className="mt-5">
        <Link
          to={`/berita/${props.slug}`}
          className="inline-flex px-4 bg-gradient-to-r from-blue-400 to-blue-300 text-white py-2 rounded-md border"
        >
          Selengkapnya
        </Link>
        <Link
          to={`/berita/update/${props.slug}`}
          className="inline-flex px-4 bg-gradient-to-r from-green-400 to-green-300 text-white py-2 rounded-md border"
        >
          Update
        </Link>
        <button
          onClick={() => {
            swal({
              title: "Are you sure?",
              text: "Once deleted, you will not be able to recover this imaginary file!",
              icon: "warning",
              buttons: true,
              dangerMode: true,
            }).then((willDelete) => {
              if (willDelete) {
                deleteHandle(props.id)
              
              } else {
                swal("Your imaginary file is safe!");
              }
            });
          }}
          className="inline-flex px-4 bg-gradient-to-r from-red-400 to-red-300 text-white py-2 rounded-md border"
        >
          delete
        </button>
      </div>
    </article>
  );
}

export default Artikel;
